body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.custom-header {
text-align:center;
font-size:25px;
font-family:'Shrikhand', cursive;
}
.save-button {
	height:40px;
	color: white;
	border: 2px solid #000;
	border-radius:4px;
	font-size:15px;
	font-family:'Shrikhand', cursive;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
	background: rgb(66, 184, 221);
	cursor:pointer;
}

.current-d {
	display:none;
}
@media screen and (max-width: 1400px) {
.data-block {
	width:30%;
	height:400px;
	float:right;
	margin-right:5%;
	border:1.5px solid #000;
	border-radius:10px;
	text-align:center;
	color:black;
	text-shadow: 0 1px 1px #FFF;
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(255,0,255,0.75) 60%);
    background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(255,0,255,0.75) 60%);	
}
}

@media screen and (max-width: 700px) {
.data-block {
	width:80%;
	height:400px;
	margin-right:10%;
	border:1.5px solid #000;
	border-radius:10px;
	text-align:center;
	background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(63,0,59,0.35) 90%);
	background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(63,0,59,0.35) 90%); 
}
}

.custom-button {
	height:56px;
	color: white;
	border: 2px solid #000;
	border-bottom-right-radius:4px;
	border-top-right-radius:4px;
	font-family:'Shrikhand', cursive;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
	background: rgb(66, 184, 221);
	cursor:pointer;
}

.custom-input {
	height:50px;
	border: 2px solid #000;
	border-bottom-left-radius:4px;
	border-top-left-radius:4px;	
	text-align:center;
}

.loader-block {
	margin-top:50px;
	display:block;
	opacity:0;
}

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
@-webkit-keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
@keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
@media screen and (max-width: 1400px) {
.display-block {
	width:30%;
	height:400px;
	float:left;
	margin-left:5%;
	border:1.5px solid #000;
	border-radius:10px;
	text-align:center;
	color:black;
	background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(63,0,59,0.35) 90%);
	background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(63,0,59,0.35) 90%); 
}
}

@media screen and (max-width: 700px) {
